.NewArea {
    width: 50%;
    margin: 20px auto;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    text-align: center;
}

.NewArea label {
    display: block;
    margin: 10px auto;
    text-align: center;
}

.NewArea input,
.NewArea textarea,
.NewArea select {
    display: block;
    width: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid black;
    outline: none;
    font: inherit;
    margin: auto;
}

.NewArea button {
    margin: 5px 0;
    padding: 10px;
    font: inherit;
    border: 1px solid #fa923f;
    background-color: transparent;
    color: #fa923f;
    cursor: pointer;
}

.NewArea button:hover,
.NewArea button:active {
    color: white;
    background-color: #fa923f;
}


